import React, {useState} from "react";
import {TOTAL_SCREENS, GET_SCREEN_INDEX} from "../../../utilities/commonUtilitis";
import ScrollService from "../../../utilities/ScrollServiceeee";
import {FaBars} from "react-icons/all";
import "./Header.css"

export default function Header(){
    const [selectedScreen, setSelectedScreen] = useState(0)
    const [showHeaderOption, setShowHeaderOption] = useState(false)

    const updateCurrentScreen = (currentScreen) => {
        if (currentScreen || !currentScreen.screenInView)
            return
        let screenIndex = GET_SCREEN_INDEX(currentScreen.screenInView)
        if(screenIndex < 0)
            return;
    }
    let currentScreenSubscription = ScrollService.currentScreenBroadCaster.subscribe(updateCurrentScreen)

    const getHeaderOptions = () =>{
        return(
            TOTAL_SCREENS.map((screen, i)=>(
                <div
                    key={screen.screen_name}
                    className={getHeaderOptionsClass(i)}
                    onClick={()=> switchScreen(i, screen)}>

                    <span>{screen.screen_name}</span>
                </div>
            ))
        )
    }

    const getHeaderOptionsClass = (index)=>{
        let classes = "component "
        if (index < TOTAL_SCREENS.length - 1)
            classes += "header-option-seperator "

        if (selectedScreen === index)
            classes += "selected-header-option "
        return classes

    }
    const switchScreen = (index, screen) => {
        let screenComponent = document.getElementById(screen.screen_name)
        if(!screenComponent)
            return;

        screenComponent.scrollIntoView({behavior: "smooth"});
        setSelectedScreen(index);
        setShowHeaderOption(false)

    }

    return(
        <div className="header-container" onClick={()=>setShowHeaderOption(!showHeaderOption)}>
            <div className="header-parent">
                <div className="header-hamburger" onClick={()=>setShowHeaderOption(!showHeaderOption)}>
                    <FaBars className="header-hamburger-bars"/>
                </div>
                <div className="header-logo">
                    <span> Andziak </span>
                </div>
                <div className={(showHeaderOption) ? "header-options show-hamburger-options" : "header-options"}>
                    {getHeaderOptions()}
                </div>
            </div>

        </div>
    )
}